.video {
  margin-left: 10px;
  margin-top: 50px;
}
p {
  font-size: 24px;
  font-weight: 700;
  color: #0c378e;
}
/* Tabs container */
.tabs-container {
  font-family: Arial, sans-serif;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}

/* Tab buttons */
.tab-button {
  padding: 0 10px 5px 10px;
  font-size: 14px;
  font-weight: 500;
  background-color: #f0f0f000;
  color: #333;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.tab-button.active {
  border-bottom: 2px solid red;
  color: #072a6e;
  font-weight: 800;
}

/* Tab content */
.tab-content {
  height: 500px;
  margin-top: 0px;
}
.middle {
  background-color: rgba(255, 255, 255, 0);
  margin-top: 40px;
}
.tabs {
  width: 500px;
  display: flex;
  justify-content: space-evenly;
}
video {
  border-radius: 30px;
}
.idea {
  height: 1px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px 0 30px;
  position: relative;
  top: -400px;
}
.idea img {
  height: 20px;
}
.bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.im1 {
  height: 100px;
  margin-left: -22px;
}
.im2 {
  height: 40px;
  margin-right: 20px;
}
