.header {
  padding: 30px 0px 0px 0px;

  width: 100%;
  background-color: rgba(250, 250, 250, 0);
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.left {
  height: 60px;
  display: flex;
  align-items: center;
}
.im {
  height: 24px;
}
.imgc {
  height: 22px;
}
.imge {
  height: 17px;
  margin-left: 1px;
  margin-bottom: -4px;
}
.imgcc {
  margin-bottom: -10px;
}
.img {
  margin: 10px;
  height: 35px;
}
.right {
  width: 500px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.btn {
  padding: 10px 40px 10px 40px;
  border-radius: 25px;
  background: linear-gradient(to bottom, #092e79, #154cbb);
  color: white;
  border: none;
  outline: none;
}
