.control-panel {
  margin-top: 20px;
  margin-bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: #092e79;
  width: 100%;
}
.mid {
  color: #092e79;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.skip {
  color: white !important;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: linear-gradient(to bottom, #092e79, #154cbb);
  display: flex;
  align-items: center;
  justify-content: center;
}
.sk {
  color: white;
  font-size: 20px;
}
.col {
  color: #092e79;
  font-size: 20px;
}
